.button-block {
  &__image {
    margin-bottom: -3px;
    width: 100%;
    height: 168px;
    object-fit: cover;
  }

  &__link {
    display: block;
    width: 100%;
    text-align: center;
    @include text($white, 14px, 20px);
    padding: 25px 10px;
    text-transform: uppercase;
    font-weight: bold;
    letter-spacing: 0.2em;
    background-color: $main-blue;
    @include transition(background-color);
    border: none;
    cursor: pointer;

    &:hover {
      background-color: $hover-blue;
    }
  }
}

@media (min-width: 1080px) {
  .button-block {
    padding: 0;

    &__image {
      width: 100%;
      height: 100%;
      max-width: 361px;
      max-height: 271px;
      object-fit: cover;
    }
  }
}

@media (min-width: 1440px) {
  .button-block {
    &__link {
      padding: 35px 10px;
    }
  }
}

